.elements{
    display: grid;
    grid-template-columns: repeat(3, .1fr);
    column-gap: 17px;
    row-gap: 20px;
    justify-content: center;
    margin-top: 4%;
    padding: 0;
}

@media (max-width: 915px) {
    .elements{
        grid-template-columns: auto;
        margin-top: 11.4%;
    }
}